<template>
  <div class="p2">
    <div class="dakuangjia">
      <div class="UP_4" id="UP_4_cotent">
        <P class="UP_4_top" style="text-align: center;">印花税申报</P>

        <div class="UP_4_Information">
          <span class="UP_4_span">纳税人信息</span>

          <p>纳税人识别号：&nbsp;&nbsp;<input type="text" readonly v-model="com.firm_code"/></p>

          <p>纳税人名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input type="text" readonly v-model="com.firm_name"/></p>
        </div>
        <div class="UP_4_Time">
          <span class="UP_4_span">税款所属时期</span>

          <P>税费所属时期起：&nbsp;&nbsp;<input type="text" :value="com.month+'-01'" readonly/>　　　　　　如： 税款所属期起：<font
            color="#FF0000">20**-**-**</font></P>

          <P>税费所属时期止：&nbsp;&nbsp;<input type="text" :value="com.month+'-31'" readonly/>　　　　　　如： 税款所属期止：<font
            color="#FF0000">20**-**-**</font></P>
        </div>
        <div class="UP_4_Upload">
          <span class="UP_4_span">纳税期限</span>

          <p>
            纳税期限：
            <select class="shenbaocishu">
              <option value="按次申报" selected="selected">按次申报</option>
              <option value="按月申报">按月申报</option>
            </select>
          </p>
        </div>
      </div>
      <div style="margin-top: 15px" class="uk-text-center">
        <router-link to="/transfer/p41">
          <button class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 填写申报表</button>
        </router-link>
        <button class="uk-button uk-button-primary"><i class="uk-icon-close"></i> 关闭</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    computed: {
      com() {
        return this.$store.state.companyCurrent.gscompany
      }
    },
    methods: {
      sel() {
        let that = this
        that.$store.commit('saveConfirm', { name: 'taxes_status', data: '按次申报' })
        J('.shenbaocishu').change(function () {
          let taxes_status = J(this).val()
          that.$store.commit('saveConfirm', { name: 'taxes_status', data: taxes_status })
        })
      }
    },
    mounted() {
      //默认纳税期限为  按次申报
      this.sel()
    }
  }
</script>

